import React from "react";
import { uniq } from "ramda";

import ImageCard from "components/customers/ImageCard";

import {
  Container,
  TitleBar,
  Title,
  Dropdown,
  List,
  Card,
  Caption,
} from "./elements";

class CaseStudyList extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      caseStudiesFiltered: props.caseStudies,
    };

    this.uniqueSortedCategories = this.getUniqueSortedCategories();
    this.handleSelectChange = this.handleSelectChange.bind(this);
  }

  getUniqueSortedCategories() {
    const { caseStudies } = this.props;

    const categories = caseStudies.map(({ fields }) => fields.customerCategory);

    return uniq(categories).sort();
  }

  handleSelectChange(e) {
    // find case studies that match filter
    const { caseStudies } = this.props;
    const selectedOption = e.currentTarget.value;

    const caseStudiesFiltered =
      selectedOption === "Select industry"
        ? caseStudies
        : caseStudies.filter(
            ({ fields }) => selectedOption === fields.customerCategory,
          );

    this.setState({ caseStudiesFiltered });
  }

  render() {
    const { caseStudiesFiltered } = this.state;
    return (
      <Container>
        <TitleBar>
          <Title>Customer Stories</Title>
          <Dropdown onChange={this.handleSelectChange}>
            <option value="Select industry">Select industry</option>
            {this.uniqueSortedCategories.map((category) => (
              <option value={category} key={category}>
                {category}
              </option>
            ))}
          </Dropdown>
        </TitleBar>
        <List>
          {caseStudiesFiltered.map(({ fields }) => (
            <Card key={fields.urlPath} to={`/customers/${fields.urlPath}/`}>
              <ImageCard
                hoverElement={Card}
                imgSrc={fields.thumbnailImage.fields.file.url}
                heightToWidthRatio={0.75}
                category={fields.customerCategory}
                logoSrc={fields.companyLogo.fields.file.url}
                // hero is 450 px, card is 240 px -> 8/15 dimensions
                logoHeight={fields.logoHeight && (fields.logoHeight * 8) / 15}
                logoWidth={fields.logoWidth && (fields.logoWidth * 8) / 15}
              />
              <Caption>{fields.caseStudyTitle}</Caption>
            </Card>
          ))}
        </List>
      </Container>
    );
  }
}

export default CaseStudyList;
