import React from "react";

import ImageCard from "components/customers/ImageCard";
import {
  Wrapper,
  Slide,
  LeftArrow,
  SlideInner,
  Body,
  Category,
  Quote,
  Speaker,
  Position,
  ImageLink,
  RightArrow,
  IndicatorList,
  Indicator,
} from "./elements";

class Slideshow extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedIndex: 0,
    };

    const { slides } = props;

    // initializing these functions here
    // if it's initialized in render, that redefines the function
    // every time it's rerendered
    this.incrementLeft = () => this.increment(-1);
    this.incrementRight = () => this.increment(1);
    this.goToIndexFn = slides.map((_, index) => () => this.goToIndex(index));
  }

  increment(step) {
    const { slides } = this.props;
    const { selectedIndex } = this.state;

    let newIndex = (selectedIndex + step) % slides.length;

    if (newIndex < 0) {
      newIndex += slides.length;
    }

    this.goToIndex(newIndex);
  }

  goToIndex(newIndex) {
    this.setState({
      selectedIndex: newIndex,
    });
  }

  render() {
    const { slides } = this.props;
    const { selectedIndex } = this.state;

    const slide = slides[selectedIndex];
    const { category, quote, speaker, url, backgroundImgSrc, logo } = slide;

    const { src, height, width } = logo;

    return (
      <Wrapper>
        <Slide>
          <LeftArrow onClick={this.incrementLeft} />
          <SlideInner>
            <Body>
              <Category>{category}</Category>
              <Quote>
                {"“"}
                {quote}
                {"”"}
              </Quote>
              <Speaker>
                {speaker.name}
                {", "}
                <Position>{speaker.position}</Position>
              </Speaker>
            </Body>
            <ImageLink to={url}>
              <ImageCard
                hoverElement={ImageLink}
                imgSrc={backgroundImgSrc}
                heightToWidthRatio={0.625}
                category="&nbsp;"
                logoSrc={src}
                logoHeight={height}
                logoWidth={width}
              />
            </ImageLink>
          </SlideInner>
          <RightArrow onClick={this.incrementRight} />
        </Slide>
        <IndicatorList>
          {slides.map((_, index) => (
            <Indicator
              key={slides[index].url}
              selected={index === selectedIndex}
              onClick={this.goToIndexFn[index]}
            >
              {"\u2022"}
            </Indicator>
          ))}
        </IndicatorList>
      </Wrapper>
    );
  }
}

export default Slideshow;
