import styled from "@emotion/styled";

import colors from "styles/colors";

export const Container = styled("div")(({ heightToWidthRatio }) => ({
  // preserve aspect ratio
  // https://www.w3schools.com/howto/howto_css_aspect_ratio.asp
  paddingTop: `${100 * heightToWidthRatio}%`,
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  position: "relative",
}));

export const Image = styled("img")(({ hoverElement }) => ({
  transition: "filter 0.2s ease-in-out",
  position: "absolute",
  height: "100%",
  width: "100%",
  objectFit: "cover",
  top: 0,
  left: 0,
  zIndex: 0,

  // when hoverElement is hovered, change properties here
  [`${hoverElement}: hover &`]: {
    filter: "brightness(50%)",
  },
}));

export const Top = styled("div")({
  position: "absolute",
  top: 0,
  width: "100%",
  display: "flex",
  alignItems: "flex-start",
  justifyContent: "space-between",
  padding: 18,
});

export const Category = styled("p")({
  color: colors.white,
  fontSize: 12,
  lineHeight: "17px",
  letterSpacing: "0.1em",
  textTransform: "uppercase",
  margin: 0,
});

export const Arrow = styled("img")({
  height: 12,
  width: 12,
  objectFit: "contain",
});

const centerPositionAbsolute = {
  position: "absolute",
  margin: "auto",
  top: 0,
  bottom: 0,
  left: 0,
  right: 0,
};

export const Logo = styled("img")(({ height, width, hoverElement }) => ({
  maxHeight: "30%",
  maxWidth: "80%",
  height,
  width,
  objectFit: "contain",
  transition: "opacity 0.2s ease-in-out",

  ...centerPositionAbsolute,

  [`${hoverElement}:hover &`]: {
    opacity: 0,
  },
}));

export const HoverText = styled("p")(({ hoverElement }) => ({
  color: colors.white,
  fontSize: 22,
  lineHeight: "31px",
  fontWeight: 500,
  opacity: 0,
  zIndex: 1,
  transition: "opacity 0.2s ease-in-out",

  ...centerPositionAbsolute,

  display: "flex",
  alignItems: "center",
  justifyContent: "center",

  [`${hoverElement}: hover &`]: {
    opacity: 1,
  },
}));
