import styled from "@emotion/styled";
import { margin, padding } from "polished";
import { Link } from "gatsby";

import colors from "styles/colors";
import { breakPoints } from "components/Grid.tsx";

import arrowDown from "assets/img/icons/black/light-fill/arrow-down.svg";

export const Container = styled("div")({
  maxWidth: 1390,
  ...padding(0, 120),
  ...margin(0, "auto"),
  display: "flex",
  flexDirection: "column",

  [breakPoints.medium]: {
    ...padding(0, 60),
  },

  [breakPoints.small]: {
    ...padding(0, 30),
  },
});

export const TitleBar = styled("div")({
  width: "100%",
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center",
  paddingBottom: 26,
  borderBottom: "1px solid #E0E0E0",
});

export const Title = styled("div")({
  color: colors.black,
  fontSize: 32,
  lineHeight: "41px",
  fontWeight: 700,

  [breakPoints.small]: {
    fontSize: 24,
    lineHeight: "35px",
  },
});

export const Dropdown = styled("select")({
  appearance: "none", // override default select
  borderRadius: 0,
  border: "1px solid #ECECEC",
  ...padding(10, 50, 10, 15),
  backgroundColor: colors.white,
  // create custom dropdown arrow
  backgroundImage: `url(${arrowDown})`,
  backgroundRepeat: "no-repeat",
  backgroundPosition: "right 15px center",
  backgroundSize: "20px 20px",

  fontSize: 12,
  lineHeight: "14px",
  fontWeight: 300,

  [breakPoints.medium]: {
    marginLeft: 26,
    fontSize: 9,
    lineHeight: "11px",
    ...padding(8, 30, 8, 10),
    backgroundPosition: "right 10px center",
    backgroundSize: "16px 16px",
  },
});

export const List = styled("div")({
  ...padding(60, 0, 130, 0),
  display: "grid",
  gridTemplateColumns: "repeat(auto-fill, minmax(240px, 1fr))",
  gridAutoFlow: "row",
  gridTemplateRows: "repeat(1fr)",
  justifyContent: "space-between",
  gridColumnGap: 90,
  gridRowGap: 90,

  [breakPoints.medium]: {
    gridColumnGap: 60,
    gridRowGap: 60,
  },

  // allow min-width to be a bit lower
  // so that it stays two to a row
  // not setting this to be the standard because it
  // makes it too narrow on wider screen widths
  "@media(max-width: 660px)": {
    gridTemplateColumns: "repeat(auto-fill, minmax(198px, 1fr))",
  },

  // force
  [breakPoints.small]: {
    gridTemplateColumns: "1fr",
    gridColumnGap: 30,
    gridRowGap: 30,
  },
});

export const Card = styled(Link)({
  transition: "box-shadow 0.2s ease-in-out",
  boxShadow: "0px 5px 15px rgba(234, 235, 236, 0.5)",

  "&:hover": {
    cursor: "pointer",
    textDecoration: "none",
    boxShadow: "0px 8px 20px rgba(234, 235, 236, 0.75)",
  },
});

export const Caption = styled("p")({
  color: colors.darkGray,
  fontSize: 22,
  lineHeight: "31px",
  fontWeight: 500,
  margin: 0,
  ...padding(24),
  borderRadius: 2,

  [breakPoints.medium]: {
    fontSize: 16,
    lineHeight: "27px",
  },
});
