import React from "react";

import arrowIcon from "assets/img/icons/white/arrow-up-right.svg";

import {
  Container,
  Image,
  Top,
  Category,
  Arrow,
  Logo,
  HoverText,
} from "./elements";

const ImageCard = ({
  hoverElement,
  imgSrc,
  heightToWidthRatio,
  category,
  logoSrc,
  logoHeight,
  logoWidth,
}) => (
  <Container heightToWidthRatio={heightToWidthRatio}>
    <Image src={imgSrc} hoverElement={hoverElement} />
    <Top>
      <Category>{category}</Category>
      <Arrow src={arrowIcon} />
    </Top>
    <Logo
      src={logoSrc}
      height={logoHeight}
      width={logoWidth}
      hoverElement={hoverElement}
    />
    <HoverText hoverElement={hoverElement}>Read more</HoverText>
  </Container>
);

export default ImageCard;
